import React from "react"
import Layout from "../Components/Layout"
import styled from 'styled-components'
import "./style.css"

const Box = styled.div({
  background: '#FAEBD7',
  height: 'auto',
  width: 'auto'
});
const Skills = ({ data:{allContentfulSkills:{nodes}} }) => {
    return (
            <Layout>
                <Box className="boxSkills">
                    <h1 className="mainHdngSkill">What can I do...</h1>
                    <section className="secSkills">
                      <div className="dvthreeSkills-1">
                        <div className="dvSkill-1">
                          <center><h3 className="HdngSkill1">Frontend</h3></center>
                            {nodes.map(skills => {
                              if(skills.skillType==="Front-End" || skills.name==="ASP .NET"){
                                return(
                                  <article id={skills.id} key={skills.id} className="artiSkills">
                                      <img src={skills.image.fluid.src} alt={skills.title} width={100} height={100}/><br/>
                                  </article>
                                )
                              }
                            })}
                        </div>
                        <div className="dvSkill-1">
                          <h3 className="HdngSkill1">server-side</h3>
                            {nodes.map(skills => {
                              if(skills.skillType==="Front-End/Back-End"){
                                return(
                                  <article id={skills.id} key={skills.id} className="artiSkills">
                                      <img src={skills.image.fluid.src} alt={skills.title} width={100} height={100}/><br/>
                                  </article>
                                )
                              }
                            })}
                        </div>
                        <div className="dvSkill-1">
                          <h3 className="HdngSkill1">Database</h3>
                            {nodes.map(skills => {
                              if(skills.skillType==="Database"){
                                return(
                                  <article id={skills.id} key={skills.id} className="artiSkills">
                                      <img src={skills.image.fluid.src} alt={skills.title} width={100} height={100}/><br/>
                                  </article>
                                )
                              }
                            })}
                        </div>
                      </div>
                      <div className="dvthreeSkills-1">
                      <div className="dvSkill-1">
                          <h3 className="HdngSkill1">{"DevOps Deployment"}</h3>
                            {nodes.map(skills => {
                              if(skills.skillType==="DevOps Deployment" || skills.skillType==="DevOps Operate"){
                                return(
                                  <article id={skills.id} key={skills.id} className="artiSkills">
                                      <img src={skills.image.fluid.src} alt={skills.title} width={100} height={100}/><br/>
                                  </article>
                                )
                              }
                            })}
                        </div>
                        <div className="dvSkill-1">
                            
                            {nodes.map(skills => {
                              if(skills.skillType==="DevOps Operate"){
                                return(
                                  <>
                                    <h3 className="HdngSkill1">{skills.skillType}</h3>
                                    <article id={skills.id} key={skills.id} className="artiSkills">
                                        <img src={skills.image.fluid.src} alt={skills.title} width={100} height={100}/><br/>
                                    </article>
                                  </>
                                )
                              }
                            })}
                          </div>
                        </div>





                     
                    </section>
                </Box>
            </Layout>
    )
}
export const query = graphql`
  {
    allContentfulSkills {
      nodes {
        name
        slug
        skillType
        image {
          fluid {
            src
          }
        }
      }
    }
  }
`
export default Skills